import { Pie } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';

function ExpenceTransactions({ expenceStatistic, fullStatistic, expenceCategories }) {
  ChartJS.register(ArcElement, Tooltip, Legend);
  const data = {
    labels: fullStatistic.filter(el => expenceCategories.some(key => el.hasOwnProperty(key))).map(el => Object.keys(el)).join().split(','),
    datasets: [
      {
        label: 'UAH',
        data: fullStatistic.filter(el => expenceCategories.some(key => el.hasOwnProperty(key))).map(el => Object.values(el)).join().split(','),
        backgroundColor: [
          'rgba(255, 99, 104, 0.5)',
          'rgba(54, 162, 235, 0.5)',
          'rgba(255, 206, 86, 0.5)',
          'rgba(75, 192, 192, 0.5)',
          'rgba(153, 102, 255, 0.5)',
          'rgba(255, 64, 140, 0.5)',
          'rgba(255, 185, 86, 0.5)',
          'rgba(86, 255, 224, 0.5)',
          'rgba(86, 86, 255, 0.5)',
          'rgba(111, 68, 37, 0.5)',
        ],
        borderColor: [
          'rgba(255, 99, 104, 1)',
          'rgba(54, 162, 235, 1)',
          'rgba(255, 206, 86, 1)',
          'rgba(75, 192, 192, 1)',
          'rgba(153, 102, 255, 1)',
          'rgba(255, 64, 140, 1)',
          'rgba(255, 185, 86, 1)',
          'rgba(86, 255, 224, 1)',
          'rgba(86, 86, 255, 1)',
          'rgba(111, 68, 37, 1)',
        ],
        borderWidth: 1,
      },
    ],
  };

  const options = {
    plugins: {
      legend: {
        position: 'right' ,
      }
    },
    redraw: true,
    maintainAspectRatio: false,
    responsive: true,
  };

  return <Pie data={data} options={options} style={{width: '15rem', height: '15rem'}}/>
}

export default ExpenceTransactions