import { Navbar, Nav, Container, ListGroup } from 'react-bootstrap';
import logo from './logo.svg';
// import lol from './img/App/door.svg';
import './App.scss';
import { Link, Navigate, Outlet } from 'react-router-dom';
import { useEffect, useState } from 'react';
import Sidebar from './Sidebar';

function App() {
  const [user, setUser] = useState([])
  const [isRedirected, setIsRedirected] = useState(false);

  useEffect(() => {
    fetch('https://dashakol88.pythonanywhere.com/api/user/account', {
      credentials: "include",
      mode: 'cors'
     }).then(data => data.json()).then(data => setUser(data.Account))
  }, [])

  function changeColour(e) {
    document.querySelectorAll('.sidebar-item').forEach(el => el.classList.remove('active'));
    e.target.classList.toggle('active');
  }

  const handleSubmit = async (event) => {
    const response = await fetch('https://dashakol88.pythonanywhere.com/api/user/logout', {
      method: 'GET',
      credentials: "include",
      mode: 'cors',
    });

    if (response.ok) {
      setIsRedirected(true);
      
    } else {
      console.log('Failed to fetch users');
    }
  };
  // let [user, setUser] = useState({});
  // useEffect(() => {
  //   let a = JSON.parse(localStorage.getItem('User'));
  //   console.log(a);
  //   setUser(a);
  // }, [])

  return (<>
    <Navbar  bg="dark" variant="dark">
      <Navbar.Brand href='#'>
        <img src={logo}
        width="30"
        height="30"
        className="d-inline-block align-top"
        />{' '}React Budget
      </Navbar.Brand>
      <Navbar.Collapse className="justify-content-end">
        {/* баланс */}
        <Navbar.Text className='d-flex '>
          <p className='text-white m-0 mr-3'>Your Balance: {user[0]?.account_balance} UAH</p>
          <p className='m-0'>Signed in as: <a href="#login" className='text-capitalize'>{user[0]?.account_owner__username}</a></p>
          {/* <Link to={'/login'}>LogOut</Link> */}
        </Navbar.Text>
      </Navbar.Collapse>
    </Navbar>
    <div className='d-flex'>
    <Sidebar changeColour={changeColour} handleSubmit={handleSubmit}/>
    <Outlet />
    </div>
    {isRedirected ? <Navigate to={`/login`}></Navigate> : ''}
  </>
  );
}

export default App;
